<template>
    <div class="search-wrapper">
          <v-form @submit.prevent='onSubmit'>
            <v-text-field
              placeholder="Поиск"
              filled
              rounded
              dense
              v-model='value'

            >
            <v-icon
                slot="append"
                color="orange"
                @click='onSubmit'
            >
                mdi-magnify
            </v-icon>
            
            </v-text-field>
            </v-form>
    </div>
</template>

<script>
export default {
    data() {
        return {value: this.$router.param}
    },

    methods: {
        onSubmit() {
            if (this.$router.history.current.path !== '/searchresoults')
                this.$router.push('/searchresoults')
            this.$emit('searchFor', this.value)
            this.$router.param = this.value
            this.value = ''
        }
    }
}
</script>

<style scoped>
  .search-wrapper {
    margin-top: 3rem;
    width: 100%;
    max-width: 500px;
  }
  .v-icon {
    cursor: pointer;
  }
  .v-text-field {
    background: white;
    height: 40px;
  }
</style>