<template>
    <header class="header"
    >
        <v-app-bar
            :color="changeColor"
            dense
            fixed
        >
            <v-container
                class='container'
            >

                <Logo/>

                <v-app-bar-nav-icon
                    @click="drawer = true"
                    v-show='menuWidth'
                >
                    <v-icon
                        color='white'
                    >menu</v-icon></v-app-bar-nav-icon>

                    <Nav 
                        v-show='!menuWidth'
                        :navItems='navItems'
                    />
                </v-container>
        </v-app-bar>
        <v-navigation-drawer
            v-model="drawer"
            fixed
            app
            temporary
        >
            <DrawerList :navItems='navItems'/>
        </v-navigation-drawer>
    </header>
</template>

<script>
import DrawerList from './DrawerList.vue'
import Nav from './Nav'
import Logo from './Logo'

export default {
  name: 'Header',
  components: {
      Nav,
      Logo,
      DrawerList
  },

  data() {
    return {
        scrollLevel: 0,
        navItems: [
            {
                title: 'Главная',
                link: '/'
            },
            {
                title: 'Клиники',
                link: '/clinics'
            },
            {
                title: 'Заболевания',
                link: '/sickness'
            }
        ],
        drawer: false,
    }
  },

    mounted() {
        window.addEventListener("scroll", this.onScroll)
    },

    beforeDestroy() {
        window.removeEventListener("scroll", this.onScroll)
    },

  computed: {
      menuWidth () {    
        const show = this.$vuetify.breakpoint.width < 900 ?
            true : false
        return show 
      },
      changeColor () {
          if (this.scrollLevel < 100 && this.$route.path === '/')
            return 'rgba(0, 0, 0, 0)'
          else
            return 'rgba(0, 0, 0, .4)'
      }
  },

  methods: {
    onScroll(e) {
        this.scrollLevel = e.target.documentElement.scrollTop
    }
  },
}

</script>

<style scoped>

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .header .v-navigation-drawer {
        background: rgba(0, 0, 0, .5);
    }

</style>
